/* ========== cards css =========== */

/* card-style-1 */
.card-style-1 {
  background   : var(--body-bg-accent);
  border-radius: 10px;
  padding      : 25px 0;
  position     : relative;
  @include transition(0.3s);

  &:hover {
    @include box-shadow(0px 0px 5px rgba(0, 0, 0, 0.1));
  }

  .card-meta {
    display      : flex;
    align-items  : center;
    margin-bottom: 15px;
    padding      : 0 30px;

    @media #{$xs} {
      padding: 0 20px;
    }

    .image {
      max-width    : 40px;
      width        : 100%;
      border-radius: 50%;
      overflow     : hidden;
      margin-right : 12px;

      img {
        width: 100%;
      }
    }

    .text {
      p {
        color: var(--body-color);

        a {
          color: inherit;

          &:hover {
            color: $primary;
          }
        }
      }
    }
  }

  .card-image {
    border-radius: 10px;
    margin-bottom: 25px;
    overflow     : hidden;

    a {
      display: block;
    }

    img {
      width: 100%;
    }
  }

  .card-content {
    padding: 0px 30px;

    @media #{$xs} {
      padding: 0px 20px;
    }

    h4 {
      a {
        color        : inherit;
        margin-bottom: 15px;
        display      : block;

        &:hover {
          color: $primary;
        }
      }
    }
  }
}

/* card-style-2 */
.card-style-2 {
  background   : var(--body-bg-accent);
  border-radius: 4px;
  padding      : 20px;
  @include transition(0.3s);

  &:hover {
    @include box-shadow(0px 0px 5px rgba(0, 0, 0, 0.1));
  }

  .card-image {
    border-radius: 4px;
    margin-bottom: 30px;
    overflow     : hidden;

    a {
      display: block;
    }

    img {
      width: 100%;
    }
  }

  .card-content {
    padding: 0px 10px;

    @media #{$xs} {
      padding: 0px;
    }

    h4 {
      a {
        color        : inherit;
        margin-bottom: 15px;
        display      : block;

        &:hover {
          color: $primary;
        }
      }
    }
  }
}

/* card-style-3 */
.card-style-3 {
  background   : var(--body-bg-accent);
  border-radius: 4px;
  padding      : 25px 30px;
  @include transition(0.3s);

  &:hover {
    @include box-shadow(0px 0px 5px rgba(0, 0, 0, 0.1));
  }

  .card-content {
    h4 {
      a {
        color        : inherit;
        margin-bottom: 15px;
        display      : block;

        &:hover {
          color: $primary;
        }
      }
    }

    a.read-more {
      font-weight: 500;
      color      : $dark;
      margin-top : 20px;

      &:hover {
        color         : $primary;
        letter-spacing: 2px;
      }
    }
  }
}

/* ======= icon-card ======== */
.icon-card {
  display      : flex;
  align-items  : center;
  background   : var(--body-bg-accent);
  padding      : 30px 20px;
  box-shadow   : 0px 5px 20px rgba(var(--shadow-color), 0.5);
  border-radius: 10px;

  &:hover {
    transform: translateY(-5px);
    filter   : brightness(101%);
  }

  &.icon-card-3 {
    display: block;
    padding: 0px;

    .card-content {
      display       : flex;
      padding       : 20px;
      padding-bottom: 0;
    }
  }

  h6 {
    @media #{$laptop} {
      font-size: 15px;
    }
  }

  h3 {
    @media #{$laptop} {
      font-size: 20px;
    }
  }

  &.icon-card-2 {
    display: block;

    .progress {
      height: 7px;

      .progress-bar {
        border-radius: 4px;
      }
    }
  }

  .icon {
    max-width    : 46px;
    width        : 100%;
    height       : 46px;
    border-radius: 10px;
    @include flex-center;
    font-size   : 24px;
    margin-right: 20px;
    background  : rgba($primary, 0.1);
    color       : $primary;
    font-weight : 700;

    @media #{$laptop} {
      margin-right: 10px;
    }

    &.purple {
      background: rgba($purple, 0.1);
      color     : $purple;
    }

    &.success {
      background: rgba($success, 0.1);
      color     : $success;
    }

    &.primary {
      background: rgba($primary, 0.1);
      color     : $primary;
    }

    &.orange {
      background: rgba($orange, 0.1);
      color     : $orange;
    }

    &.opacity-100 {
      &.purple {
        background: $purple;
        color     : $white;
      }

      &.success {
        background: $success;
        color     : $white;
      }

      &.primary {
        background: $primary;
        color     : $white;
      }

      &.orange {
        background: $orange;
        color     : $white;
      }

      &.deep-blue {
        background: $deep-blue;
        color     : $white;
      }
    }
  }
}

.navigation-card {
  a {
    padding: 0;

    .content {
      padding: 10px 0;
    }

    .icon {
      height       : 60px;
      border-radius: 0px;
    }
  }
}