/* ========== Buttons css ========== */

/* buttons base styles */
.main-btn {
  display       : inline-block;
  text-align    : center;
  white-space   : nowrap;
  vertical-align: middle;
  @include user-select(none);
  padding      : 15px 45px;
  font-weight  : 500;
  font-size    : 14px;
  line-height  : 24px;
  border-radius: 12px;
  cursor       : pointer;
  z-index      : 5;
  transition   : all 0.4s ease-in-out;
  border       : 1px solid transparent;
  overflow     : hidden;

  &>i {
    vertical-align: middle;
    margin-bottom : 3px;
  }

  &:hover {
    color: inherit;
  }
}

/* buttons hover effect */
.btn-hover {
  position: relative;
  overflow: hidden;

  &::after {
    content      : "";
    position     : absolute;
    width        : 0%;
    height       : 0%;
    border-radius: 50%;
    background   : rgba(255, 255, 255, 0.05);
    top          : 50%;
    left         : 50%;
    padding      : 50%;
    z-index      : -1;
    @include transition(0.3s);
    @include transform(translate3d(-50%, -50%, 0) scale(0));
  }

  &:hover {
    &::after {
      @include transform(translate3d(-50%, -50%, 0) scale(1.3));
    }
  }
}

/* primary buttons */
.primary-btn {
  background: $primary;
  color     : $white;

  &:hover {
    color: $white;
  }
}

.primary-btn-outline {
  background  : transparent;
  color       : $primary;
  border-color: $primary;

  &:hover {
    color     : $white;
    background: $primary;
  }
}

/* secondary buttons */
.secondary-btn {
  background: $secondary;
  color     : $white;

  &:hover {
    color: $white;
  }
}

.secondary-btn-outline {
  background  : transparent;
  color       : $secondary;
  border-color: $secondary;

  &:hover {
    color     : $white;
    background: $secondary;
  }
}

/* success buttons */
