  }
}

.danger-btn-outline {
  background  : transparent;
  color       : $danger;
  border-color: $danger;

  &:hover {
    color     : $white;
    background: $danger;
  }
}

/* warning buttons */
.warning-btn {
  background: $warning;
  color     : $white;

  &:hover {
    color: $white;
  }
}

.warning-btn-outline {
  background  : transparent;
  color       : $warning;
  border-color: $warning;

  &:hover {
    color     : $white;
    background: $warning;
  }
}

/* info buttons */
.info-btn {
  background: $info;
  color     : $white;

  &:hover {
    color: $white;
  }
}

.info-btn-outline {
  background  : transparent;
  color       : $info;
  border-color: $info;

  &:hover {
    color     : $white;
