/* dark buttons */
.dark-btn {
  background: $dark;
  color     : $white;

  &:hover {
    color: $white;
  }
}

.dark-btn-outline {
  background  : transparent;
  color       : var(--body-color);
  border-color: var(--body-color);

  &:hover {
    color     : $white;
    background: $dark;
  }
}

/* light buttons */
.light-btn {
  background: $light;
  color     : var(--body-color);

  &:hover {
    color: var(--body-color);
  }
}

.light-btn-outline {
  background  : transparent;
  color       : var(--body-color);
  border-color: $light;

  &:hover {
