    background: $light;
  }
}

/* active buttons */
.active-btn {
  background: $active;
  color     : $white;

  &:hover {
    color: $white;
  }
}

.active-btn-outline {
  background  : transparent;
  color       : $active;
  border-color: $active;

  &:hover {
    color     : $white;
    background: $active;
  }
}

/* deactive buttons */
.deactive-btn {
  background: $deactive;
  color     : $active;

  &:hover {
    color: $active;
  }
}

.deactive-btn-outline {
  background  : transparent;
  color       : $active;
  border-color: $deactive;

  &:hover {
    color     : $active;
    background: $deactive;
  }
