/* =========  square-btn ========= */
.square-btn {
  border-radius: 0px;
}

/* =========  rounded-md ========= */
.rounded-md {
  border-radius: 10px;
}

/* =========  rounded-full ========= */
.rounded-full {
  border-radius: 30px;
}

/* ========== buttons group css ========= */
.buttons-group {
  display  : flex;
  flex-wrap: wrap;
  margin   : 0 -10px;

  li {
    margin: 10px;
  }
}

/* ====== Status Button ====== */
.status-btn {
  padding      : 7px 15px;
  border-radius: 30px;
  font-size    : 14px;
  font-weight  : 400;

  &.primary-btn {
    color     : $white;
    background: rgba($primary, 1);
  }

  &.active-btn {
    color     : $primary;
    background: rgba($primary, 0.1);
  }

  &.close-btn {
    color     : $danger;
    background: rgba($danger, 0.1);
  }

  &.warning-btn {
    color     : $warning;
    background: rgba($warning, 0.1);
  }

  &.info-btn {
    color     : $info;
    background: rgba($info, 0.1);
  }

  &.success-btn {
    color     : $success;
    background: rgba($success, 0.1);
  }

  &.secondary-btn {
    color     : $secondary;
    background: rgba($secondary, 0.1);
  }

  &.dark-btn {
    color     : var(--body-color);
    background: rgba($dark, 0.1);
  }

  &.orange-btn {
    color     : $orange;
    background: rgba($orange, 0.1);
  }
}

.btn-sm {
  padding    : 0px 10px;
  font-weight: 400;
}

.btn-md {
  padding    : 5px 20px;
  font-weight: 400;
}

.btn-empty {
  padding: 0px;
  border : none;
  color  : var(--body-color);

  &:hover {
    color: $primary;
  }
}

.z-index-1 {
  z-index: -1;
}

.btn-hollow {
  background: white;
  border    : 1px solid $primary;
  color     : var(--body-color);

  &::after {
    content      : "";
    position     : absolute;
    width        : 0%;
    height       : 0%;
    border-radius: 50%;
    background   : $primary;
    top          : 50%;
    left         : 50%;
    padding      : 50%;
    z-index      : -1;
    border-radius: 12px;
    @include transition(0.3s);
    @include transform(translate3d(-50%, -50%, 0) scale(0));
  }

  &:hover {
    color: white;

    &::after {
      @include transform(translate3d(-50%, -50%, 0) scale(1.3));
    }
  }
}